@keyframes show {
  0% {
    opacity: 0.0; }
  50% {
    opacity: 0.0; }
  100% {
    opacity: 1.0; } }

@keyframes uToD {
  0% {
    top: -100px;
    opacity: 0.0; }
  50% {
    top: -100px;
    opacity: 0.0; }
  100% {
    top: 0;
    opacity: 1.0; } }

@keyframes dToU {
  0% {
    bottom: -100px;
    opacity: 0.0; }
  50% {
    bottom: -100px;
    opacity: 0.0; }
  100% {
    bottom: 0;
    opacity: 1.0; } }

@keyframes lToR {
  0% {
    left: -100px;
    opacity: 0.0; }
  50% {
    left: -100px;
    opacity: 0.0; }
  100% {
    left: 0;
    opacity: 1.0; } }

@keyframes rToL {
  0% {
    right: -100px;
    opacity: 0.0; }
  50% {
    right: -100px;
    opacity: 0.0; }
  100% {
    right: 0;
    opacity: 1.0; } }

