/* var */
.container {
  max-width: 600px;
  height: 500px;
  margin: 0 auto; }

.addBlood {
  animation: blood 3s; }

.screen {
  position: relative;
  width: 100%;
  height: calc(100% - 200px); }
  .screen > * {
    display: inline-block;
    width: calc( 50%);
    height: calc( 50%);
    border: 1px solid red; }
  .screen .blood {
    width: calc( 50%);
    height: calc( 30%);
    background: yellow; }
    .screen .blood h3 {
      width: 100%;
      height: 30px;
      margin: 0; }
    .screen .blood span:before {
      content: "HP:"; }
    .screen .blood .hp {
      position: absolute;
      top: 35px;
      right: 0;
      left: 30px;
      height: calc(100% - 30px);
      background: red;
      height: 15px;
      border: 1px solid black;
      border-radius: 10px; }
    .screen .blood:nth-child(1) {
      position: absolute;
      top: 0;
      left: 0;
      animation: uToD 2s;
      overflow: hidden; }
    .screen .blood:nth-child(4) {
      position: absolute;
      bottom: 0;
      right: 0;
      animation: dToU 2s;
      overflow: hidden; }
  .screen .monster {
    width: calc( 50%);
    height: calc( 70%);
    background: blue; }
    .screen .monster:nth-child(2) {
      position: absolute;
      top: 0;
      right: 0;
      animation: rToL 1s; }
    .screen .monster:nth-child(3) {
      position: absolute;
      bottom: 0;
      left: 0;
      animation: lToR 1s; }

.menu {
  animation: show 3s;
  width: calc(100%);
  height: calc(200px);
  border: 2px solid black;
  padding: 2px;
  border-radius: 5px; }
  .menu > * {
    float: left;
    display: inline-block;
    width: calc(50%);
    height: calc(100%); }
  .menu .right .actions {
    display: inline-block;
    float: left;
    padding-right: 10px;
    width: calc(50%);
    height: calc(50%);
    line-height: 100px;
    text-align: center;
    font-size: 30px;
    border-radius: 5px; }
    .menu .right .actions:before {
      content: '';
      display: inline-block;
      opacity: 0.0;
      margin: 0;
      border-color: transparent transparent transparent #000000;
      border-style: solid solid solid solid;
      border-width: 10px;
      /* 設定 width、height 可更好理解原理 */
      height: 0;
      width: 0; }
    .menu .right .actions:hover:before {
      opacity: 1.0; }
    .menu .right .actions:nth-child(1) {
      border-top: 2px solid black;
      border-left: 2px solid black; }
    .menu .right .actions:nth-child(2) {
      border-top: 2px solid black;
      border-right: 2px solid black; }
    .menu .right .actions:nth-child(3) {
      border-bottom: 2px solid black;
      border-left: 2px solid black; }
    .menu .right .actions:nth-child(4) {
      border-bottom: 2px solid black;
      border-right: 2px solid black; }
